//store.js
import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersist from '../plugins/vuex-persist'

Vue.use(Vuex)

const vuexPersist = new VuexPersist({
    key: 'rsl-app',
    storage: localStorage
})
/*
username: "shannon",
        password: "password",
*/

export default new Vuex.Store({
    state: {
        username: "shannon",
        password: "password",
        loggedIn: false,
        users:{
            "shannon":{
                username: "shannon",
                password: "password",
                firstname: '',
                lastname: '',
                adfid: '',
                email: '',
            }
        }
    },
    getters: {
        username: state => state.username,
        password: state => state.password,
        loggedIn: state => state.loggedIn,
        userData: (state) => ( username ) => {
            return state.users[username]
        }
    },
    mutations: {
        LOGIN_USER(state, data) {
            if(!state.users.hasOwnProperty(data.username)){
                alert("User doesn't exist");
            }else{
                if(state.users[data.username].username == data.username && state.users[data.username].password == data.password){
                    state.loggedIn = true
                    state.username = data.username
                }else{
                    console.log(state.users[data.username])
                    console.log(state.users[data.username])
                }
            }
        },
        LOGOUT_USER(state, data) {
            //console.log("logout mutation")
            state.loggedIn = false
            //console.log(state.loggedIn)
        },
        REGISTER_USER(state, data) {
            //check for existing username
            if(state.users.hasOwnProperty(data.username)){
                alert("User already exists");
            }else{
                state.users[data.username] = data;
            }
        },
    },
    actions: {
        login: ({commit}, context) => {
            commit('LOGIN_USER', context)
        },
        logout: ({commit}, context) => {
            console.log("logout action running")
            commit('LOGOUT_USER', context)
        },
        register: ({commit}, context) => {
            commit('REGISTER_USER', context)
        },
    },
    plugins: [vuexPersist.plugin]
})



