import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/pages/Home'
import About from '@/components/pages/About'
import Contact from '@/components/pages/Contact'
import Login from '@/components/pages/Login'
import Register from '@/components/pages/Register'
import Profile from '@/components/pages/Profile'
import SignOut from '@/components/pages/SignOut'
import NotFound from '@/components/pages/NotFound'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
        path: '/',
        name: 'Home',
        component: Hello
        },
        {
            path: '/about',
            name: 'About',
            component: About
        },
        {
            path: '/contact',
            name: 'Contact',
            component: Contact
        },
        {
            path: '/login',
            name: 'Login',
            component: Login
        },
        {
            path: '/register',
            name: 'Register',
            component: Register
        },
        {
            path: '/profile/',
            name: 'Profile',
            component: Profile
        },
        {
            path: '/profile/:tab',
            redirect: '/profile'
        },
        {
            path: '/signout',
            name: 'SignOut',
            component: SignOut
        },
        {
            path: '/static',
        },
        {
            path: '/notfound',
            name: 'NotFound',
            component: NotFound
        },
        { path: '/home', redirect: '/' },
        { path: '*', redirect: '/notfound' }


    ]
})



