<template>
<b-container class="register">
    <header class="masthead">
      <div class="container">
        <div class="intro-text">
        </div>
      </div>
    </header>
    <b-row>
        <b-col></b-col>
        <b-col><h2>Register</h2></b-col>
        <b-col></b-col>
    </b-row>
    <b-row>
        <b-col>
            <b-form @submit="onSubmit">
    <b-form-group
      id="registration-form"
      vertical
      description="Registration Form."
      label="Please enter your details to register"
      label-for="username"
    >
    <b-row class="form-row">
        <b-col sm="2"><label for="input-small">First Name:</label></b-col>
        <b-col sm="4">
        <b-form-input id="firstname" placeholder="Enter your First Name" v-model.trim="firstname"></b-form-input>
        </b-col>
        <b-col sm="2"><label for="input-small">Last Name:</label></b-col>
        <b-col sm="4">
        <b-form-input id="lastname" placeholder="Enter your Last Name" v-model.trim="lastname"></b-form-input>
        </b-col>
    </b-row>
    <b-row class="form-row">
        <b-col sm="2"><label for="input-small">ADF ID:</label></b-col>
        <b-col sm="4">
            <b-form-input id="adfid" placeholder="Enter your ADF ID" v-model.trim="adfid"></b-form-input>
        </b-col>
        <b-col sm="2"><label for="input-small">Email Address:</label></b-col>
        <b-col sm="4">
            <b-form-input id="email" placeholder="Enter your Email address" v-model.trim="email"></b-form-input>
        </b-col>
    </b-row>
    <b-row class="form-row">
        <b-col sm="2"><label for="input-small">Username:</label></b-col>
        <b-col sm="4">
            <b-form-input id="username" placeholder="Enter your new Username" v-model.trim="username"></b-form-input>
        </b-col>
        <b-col sm="2"><label for="input-small">Password:</label></b-col>
        <b-col sm="4">
            <b-form-input id="password" placeholder="Enter your new Password" v-model.trim="password"></b-form-input>
        </b-col>
    </b-row>
    <b-row class="form-row">
        <b-col sm="4">
        </b-col>
        <b-col sm="4">
        <b-button type="submit" variant="primary">Submit</b-button>
        <b-link class="nav-link" href="/login">Login</b-link></b-col>
        <b-col sm="4">
        </b-col>
    </b-row>
  </b-form-group>
            </b-form>
  </b-col>
        
    </b-row>
</b-container>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
        username: '',
        password: '',
        state: {

        },
        firstname: '',
        lastname: '',
        adfid: '',
        email: '',
        

    }
  },
  methods:{
      onSubmit (evt) {
          evt.preventDefault();
          this.$store.dispatch('register', this.$data )
          //alert(this.password);
          //show login message
          this.$store.dispatch('login', this.$data )
          let newLoggedIn = this.$store.getters.loggedIn
          if(newLoggedIn){
            this.$router.push("profile")
          }
      },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

header.masthead{
    height:15vh;
}

header.masthead .intro-text{
    padding-top: 5vh;
}

.register.container{
    max-width: 100%;
}

.register label{
    font-weight: 700;
}

.register .form-row{
    margin-left:10%;
    margin-right:10%;
    margin-top:2em;
}
</style>



