<template>
    <div id="header-container">
    <b-navbar v-scroll="handleScroll" toggleable="md"  v-bind:class="{ 'navbar-shrink': navShrink } " class="navbar navbar-expand-lg navbar-dark fixed-top" id="mainNav">
        <div class="container">

        
        <b-navbar-toggle target="nav_collapse" class="navbar-toggler navbar-toggler-right">
            Menu
          <font-awesome-icon icon="bars" />
        </b-navbar-toggle>

        <b-navbar-brand href="/home">Vetnet</b-navbar-brand>

        <b-collapse is-nav id="nav_collapse" >

            <b-navbar-nav class="navbar-nav text-uppercase ml-auto">
            <b-nav-item class="nav-link js-scroll-trigger" href="/home">Home</b-nav-item>
            <b-nav-item class="nav-link js-scroll-trigger" href="/about">About</b-nav-item>
            <b-nav-item class="nav-link js-scroll-trigger" href="/contact" disabled>Contact</b-nav-item>
            </b-navbar-nav>

            <!-- Right aligned nav items -->
            <b-navbar-nav class="ml-auto">

            <b-nav-form>
                <b-form-input size="sm" class="mr-sm-2" type="text" placeholder="Search"/>
                <b-button size="sm" class="my-2 my-sm-0" type="submit">Search</b-button>
            </b-nav-form>

            <!--<b-nav-item-dropdown text="Members" right>
                <b-dropdown-item href="#">Login</b-dropdown-item>
                <b-dropdown-item href="#">Sign Up</b-dropdown-item>
                <b-dropdown-item href="#"></b-dropdown-item>
                <b-dropdown-item href="#">Get Help</b-dropdown-item>
            </b-nav-item-dropdown>-->

            <b-nav-item-dropdown right>
                <!-- Using button-content slot -->
                <template slot="button-content">
                <em>Vetnet Members</em>
                </template>
                <template v-if="loggedIn">
                <b-dropdown-item href="/profile">Profile</b-dropdown-item>
                <b-dropdown-item @click="signOut" href="/signout">Signout</b-dropdown-item>
                </template>
                <template v-else>
                <b-dropdown-item href="/login">Login</b-dropdown-item>
                <b-dropdown-item href="/register">Register</b-dropdown-item>
                </template>
            </b-nav-item-dropdown>
            </b-navbar-nav>

        </b-collapse>
        </div>
    </b-navbar>
</div>
</template>

<script>
export default {
  name: 'app-header',
  data () {
    return {
      msg: 'Header',
      navShrink: false
    }
  },
  computed: {
    loggedIn () {
        return this.$store.getters.loggedIn;
    }
  },
  methods:{
      signOut (evt) {
          evt.preventDefault();
          this.$store.dispatch('logout', null )
          //alert(this.password);
          let newLoggedIn = this.$store.getters.loggedIn
          if(!newLoggedIn){
            this.$router.push("signout")
          }
      },
      handleScroll: function (evt, el) {
        if (window.scrollY > 100) {
            /*
            el.setAttribute(
            'style',
            'opacity: 1; transform: translate3d(0, -10px, 0)'
            )*/
            this.navShrink = true
            //console.log("Shrink True")
        }else{
            /*
            el.setAttribute(
            'style',
            'opacity: 0.5; transform: translate3d(0, 10px, 0)'
            )*/
            this.navShrink = false
            //console.log("Shrink False")
        }
        return window.scrollY > 100
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>



h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}



</style>



