<template>
  <div class="profile">
      <header class="masthead">
      <div class="container">
        <div class="intro-text">
            
        </div>
      </div>
    </header>
    <b-container class="profile-layout">
        <b-row>
            <b-col sm="2" class="sidebar">
                <b-nav vertical class="navbar navbar-dark" id="sideNav">
                    <h4 class="sidebar-heading">Welcome, {{username}}</h4>
                    <b-nav-item class="nav-link" v-b-toggle.collapse1 @click="clickedProfileLink" href="/profile/brief" active>Daily Brief</b-nav-item>
                    <b-nav-item class="nav-link" v-b-toggle.collapse2 @click="clickedProfileLink" href="/profile/comms">Comms Check</b-nav-item>
                    <b-nav-item class="nav-link" v-b-toggle.collapse3 @click="clickedProfileLink" href="/profile/downrange">Down Range</b-nav-item>
                    <b-nav-item class="nav-link" v-b-toggle.collapse4 @click="clickedProfileLink" href="/profile/siterep">Sitrep</b-nav-item>
                    <b-nav-item class="nav-link" v-b-toggle.collapse5 @click="clickedProfileLink" href="/profile/tracker">Battle Tracker</b-nav-item>
                </b-nav>
            </b-col>
            <b-col sm="10" class="content">
                <b-collapse id="collapse1" accordion="my-accordion" role="tabpanel" class="mt-2" v-bind:visible="showBrief">
                    <b-card>
                        <h1 class="card-text">Daily Brief</h1>
                        <b-row>
                           <b-col sm="4" class="brief-card-container">
                               <b-card>
                                   {{userData.firstname}}
                                   {{userData.lastname}}
                               </b-card>
                               </b-col>
                           <b-col sm="4" class="brief-card">Other Important Notice</b-col>
                           <b-col sm="4" class="brief-card"></b-col>     
                        </b-row>
                        <b-row>
                           <b-col sm="4" class="brief-card-container">
                               <b-card>
                                   Some Data Here
                               </b-card>
                               </b-col>
                           <b-col sm="4" class="brief-card">Other Important Notice</b-col>
                           <b-col sm="4" class="brief-card"></b-col>     
                        </b-row>
                        <b-row>
                           <b-col sm="4" class="brief-card-container">
                               <b-card>
                                   Some Data Here
                               </b-card>
                               </b-col>
                           <b-col sm="4" class="brief-card">Other Important Notice</b-col>
                           <b-col sm="4" class="brief-card"></b-col>     
                        </b-row>
                        <b-row>
                           <b-col sm="4" class="brief-card-container">
                               <b-card>
                                   Some Data Here
                               </b-card>
                               </b-col>
                           <b-col sm="4" class="brief-card">Other Important Notice</b-col>
                           <b-col sm="4" class="brief-card"></b-col>     
                        </b-row>
                    </b-card>
                </b-collapse>
                <b-collapse id="collapse2" accordion="my-accordion" role="tabpanel" class="mt-2" v-bind:visible="showComms">
                    <b-card>
                        <h1 class="card-text">Comms Check</h1>
                    </b-card>
                </b-collapse>
                <b-collapse id="collapse3" accordion="my-accordion" role="tabpanel" class="mt-2" v-bind:visible="showRange">
                    <b-card>
                    <h1 class="card-text">Down Range</h1>                    
                    </b-card>
                </b-collapse>
                <b-collapse id="collapse4" accordion="my-accordion" role="tabpanel" class="mt-2" v-bind:visible="showSitrep">
                    <b-card>
                        <h1 class="card-text">Sitrep</h1>
                    </b-card>
                </b-collapse>
                <b-collapse id="collapse5" accordion="my-accordion" role="tabpanel" class="mt-2" v-bind:visible="showTracker">
                    <b-card>
                        <h1 class="card-text">Battle Tracker</h1>
                    </b-card>
                </b-collapse>
                
            </b-col>
        </b-row>
    </b-container>
      
  </div>
</template>

<script>


export default {
  name: 'profile',
  data() {
        return {
            showBrief:true,
            showComms:false,
            showRange:false,
            showSitrep:false,
            showTracker:false,
        }
    },
    computed: {
        loggedIn () {
            return this.$store.getters.loggedIn;
        },
        username (){
            return this.$store.getters.username;
        },
        userData () {
            return this.$store.getters.userData(this.username);
        },
        tab () {
            return this.$route.params.tab;
        }
    },
    mounted(){
        this.whenReady();
    },
    methods: {
         /**
          * Finish bootstrapping the application.
          */
         whenReady() {
            if(!this.loggedIn){
                this.$router.push("home")
            }
            //toggle correct panels
            if(typeof tab != "undefined") {
                console.log(tab)
            }
         },
         clickedProfileLink(evt) {
            evt.preventDefault();
            //toggle correct panels
            
         },
    }
    
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

#mainNav{
    background-color: #262a2d;
}

header.masthead{
    height:15vh;
}

header.masthead .intro-text{
    padding-top: 5vh;
}

.profile-layout.container{
    max-width: 100%;
}

.sidebar{
    background-color: #262a2d; 
}
.sidebar-heading{
    margin-top:5vh;
}

.sidebar-heading, .sidebar a{
    color:white;
}

#sideNav .nav-link {
    font-size: 90%;
    font-weight: 400;
    padding: 0.75em 0;
    letter-spacing: 1px;
    color: white;
    font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
}
#sideNav .nav-link:hover, #sideNav .nav-link:active{
    color:#fed136;
}

.profile-layout .row, .profile-layout .container{
    height:85vh;
}

.mt-2.show .card{
    min-height: 30%;
}

.mt-2.show .card .row{
    height:initial;
    max-height: 60vh;
    margin-top: 5vh
}

.brief-card-container .card{
    min-height: 20%;
}

.brief-card-container{
    max-height: 20%
}

</style>



