<template>
  <div class="notfound">
      <header class="masthead">
      <div class="container">
        <div class="intro-text">
            <h2>Error!</h2>
            <h4>{{ msg }}</h4>
        </div>
      </div>
    </header>
  </div>
</template>

<script>
export default {
  name: 'notfound',
  data () {
    return {
      msg: '404 - Page not found!'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



