
<template>
  <div id="app">
    <app-header></app-header>
    <template v-if="showUserControls">
        <router-view></router-view>
    </template>
    <template v-else>
        <router-view></router-view>
    </template>
    <app-footer></app-footer>
  </div>
</template>

<script>
import Vue from 'vue'
import Vue2Sidebar from 'vue2-sidebar';
//import Vuex from 'vuex'
//import BootstrapVue from 'bootstrap-vue'
import Header from '@/components/common/Header'
import Footer from '@/components/common/Footer'
////import 'bootstrap/dist/css/bootstrap.css'
//import 'bootstrap-vue/dist/bootstrap-vue.css'
import store from './store/store.js'
//Vue.use(Vuex);
import { library } from '@fortawesome/fontawesome-svg-core'
import { faBars, faCircle, faShoppingCart, faLaptop, faLock} from '@fortawesome/free-solid-svg-icons'
import { faFacebook, faTwitter, faLinkedin  } from '@fortawesome/free-brands-svg-icons'
import { FontAwesomeIcon, FontAwesomeLayers } from '@fortawesome/vue-fontawesome'


//CSS Imports
import './assets/css/agency.css';



//Declare imports for components on Vue
Vue.component('app-header', Header)
Vue.component('app-footer', Footer)
//TODO Change this siderbar, don't like it.
Vue.use(Vue2Sidebar)

//Font Awesome icons
library.add(faBars, faCircle, faShoppingCart, faLaptop, faLock)
library.add(faFacebook, faTwitter, faLinkedin)
Vue.component('font-awesome-icon', FontAwesomeIcon)
Vue.component('font-awesome-layers', FontAwesomeLayers)
Vue.config.productionTip = false


//Directives
Vue.directive('scroll', {
  inserted: function (el, binding) {
    let f = function (evt) {
      if (binding.value(evt, el)) {
        //Keep this listener
        //window.removeEventListener('scroll', f)
      }
    }
    window.addEventListener('scroll', f)
  }
})

//Set up Vuex store
export default {
  name: 'app',
  store: store,
  data () {
    return {
      loggedIn: this.$store.getters.loggedIn,
      links: [
                {
                    label: 'Home', href: '/home', links: [
                        
                    ],
                },
                {label: 'Link #2', href: '#/foo', icon: 'fa-glass'},
                {separator: true},
                {label: 'Link #3', path: '/link3', class: 'list-group-item-danger'},
                {label: 'Link #4', href: '/link4'},
                {label: 'Router', path: '/foo'},
                {label: 'Link #6', href: '/#test', button: true},
            ],
            header: [
                {label: 'Header #1', path: '/foo'},
                {
                    label: 'Header #2', href: '#/header2', links: [
                        {label: 'Header #2.1', href: '#/header21'},
                        {label: 'Header #2.2', href: '#/header22'},
                        {separator: true},
                        {label: 'Header #2.13', href: '#/header23'},
                    ]
                },
                {label: 'Header #5', href: '#/header3', icon: 'fa-glass', button: true},
            ],
            heading:"Hello " + this.$store.getters.username,
            showHeader: true
    }
  },
  ready() {
    
  },
  methods:{
      hideSidebar (evt) {
          this.$refs.sidebar.toggleSidebar();
      },
      hideSecondaryHeader() {

      }
  },
  computed: {
    showUserControls () {
        let loggedIn = this.$store.getters.loggedIn
        let route = this.$route.matched[0]
        if(loggedIn && route.path == '/profile'){
            this.showHeader = false
            return true;
        }else{
            console.log(this.$route.matched)
            console.log(loggedIn)
            return false;
        }
        
    }
  },
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 0;
}

#sidebar {
    background: #36454f!important;
}

</style>



