<template>
  <div class="contact">
    <h2>Contact Us</h2>
    <h4>{{ msg }}</h4>
    
    
  </div>
</template>

<script>
export default {
  name: 'contact',
  data () {
    return {
      msg: 'Contact Form'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



