<template>
<b-container class="login">
    <header class="masthead">
      <div class="container">
        <div class="intro-text">
        </div>
      </div>
    </header>
    <b-row>
        <b-col></b-col>
        <b-col><h2>Login</h2></b-col>
        <b-col></b-col>
    </b-row>
    <b-row>
        <b-col></b-col>
        <b-col>
            <b-form @submit="onSubmit">
                <b-form-group
                    id="fieldset1"
                    vertical
                    description="Main User Login Form."
                    label="Enter your username &amp; password"
                    label-for="input1"
                    >
                    <b-form-input id="username" v-model.trim="username"></b-form-input>
                    <b-form-input id="password" type="password" v-model.trim="password"></b-form-input>
                    <b-button type="submit" variant="primary">Submit</b-button>
                    <b-link class="nav-link" href="/register">Register</b-link>
                    </b-form-group>
            </b-form>
    </b-col>
        <b-col></b-col>
    </b-row>
</b-container>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
        username: '',
        password: '',
        store: this.$store
    }
  },
  methods:{
      onSubmit (evt) {
          evt.preventDefault();
          this.$store.dispatch('login', this.$data )
          //alert(this.password);
          let newLoggedIn = this.$store.getters.loggedIn
          if(newLoggedIn){
            this.$router.push("profile")
          }else{
              alert("Password Incorrect")
          }
      },
      
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

header.masthead{
    height:15vh;
}

header.masthead .intro-text{
    padding-top: 5vh;
}

.login.container{
    max-width: 100%;
}
</style>



